import { Box, Divider, Grid } from "@mui/material";
import sortBy from "lodash/sortBy";
import { useOutletContext } from "react-router";
import { v4 as uuidv4 } from "uuid";
import { useAuth } from "../../../../auth";
import DataListBox from "../../../../components/DataListBox";


const WorkspaceInfoGridItem = ({ workspace, loading }) => {

    const { inject } = useAuth();


    return (
        <DataListBox
            title="عمومی"
            loading={loading}
            data={sortBy([
                { title: 'شناسه', priority: 100, description: workspace && workspace.identifier },
                // { title: 'شماره همراه', priority: 200, description: person.phone ? <SpoilerText><Text>{person.phone}</Text></SpoilerText> : '-' },
                // { title: 'آدرس ایمیل', priority: 300, description: person.email ? <SpoilerText><Text>{person.email}</Text></SpoilerText> : '-' },
                ...(inject('Core', 'WorkspaceDetails.Overview.DataList', { workspace, loading }) || [])
                // { title: 'آدرس', description: <Text>{person.fullAddress}</Text> || '-' },
                // { title: 'یادداشت', description: <Text>{person.note}</Text> || '-' },
            ], o => o.priority)}
        />
    )
}


const Overview = props => {

    const { inject } = useAuth();

    const { workspace, loading } = useOutletContext();


    return (
        <Box sx={{ p: 2 }}>
            <Grid container columns={{ xs: 1, sm: 2, md: 3 }}>
                {[
                    <WorkspaceInfoGridItem workspace={workspace} loading={loading} priority={100} {...props} />,
                    ...(inject('Core', 'WorkspaceDetails.Overview.Grid', { workspace, loading }) || [])
                ].map((child, index) => (
                    <Grid item xs={1} key={index}>
                        {child}
                    </Grid>
                )).reduce((prev, curr) => ([prev, <Divider orientation="vertical" flexItem sx={{ mr: '-1px', display: { xs: 'none', sm: 'flex' } }} key={uuidv4()} />, curr]))}
            </Grid>
        </Box>
    )
}

export default Overview;